.intro {
  padding-top: 230px;
  padding-bottom: 500px;
  background: url('../img/intro--bg.png') no-repeat;
  background-size: cover;
  @include r(767) {
    padding-top: 180px;
    padding-bottom: 300px;
    background-position: 20% 50%; }
  &__head {
    display: flex;
    flex-direction: column;
    align-items: center;
    h1 {
      font-size: 39.6px;
      font-weight: bold;
      text-transform: uppercase;
      line-height: 1.79;
      color: $slate-grey;
      padding-bottom: 25px;
      @include r(767) {
        font-size: 26px; } }
    p {
      color: $slate-grey;
      font-size: 16px;
      line-height: 1.07;
      max-width: 640px;
      padding-bottom: 40px;
      @include r(767) {
        font-size: 14px; } } }
  &__btn {
    a {
      padding: 15px 17px;
      display: inline-block;
      color: $bluey-purple;
      font-size: 16px;
      line-height: 1.07;
      border-radius: 5px;
      border: 1px solid $bluey-purple;
      box-shadow: 0px 2px 10px 0 rgba(49, 47, 47, 0.16);
      @include r(767) {
        background: $bluey-purple;
        color: $white; }
      &:hover {
        color: $pastel-red-two;
        border: 1px solid $pastel-red-two; }
      i {
        padding-right: 10px; } } } }
