.header-toolbar {
  width: 100%;
  padding: 10px 0;
  background: #e8e6ea;
  position: relative;
  &.toolbar {
    background: #faf8f8;
    @include r(767) {
      background: none; } }
  @include r(767) {
    position: absolute;
    top: 80px;
    background: none; }
  &__block {
    display: flex;
    justify-content: space-between;
    align-items: center;
    @include r(767) {
      justify-content: space-around; }
    @include r(430) {
      flex-direction: column; }
    a {
      font-size: 16px;
      font-weight: 500;
      color: $battleship-grey-two;
      @include r(1024) {
        &:nth-child(4) {
          display: none; } }
      @include r(767) {
        font-size: 14px;
        &:nth-child(1), &:nth-child(2), {
          display: none; }
        &:nth-child(4) {
          display: block; } }
      @include r(430) {
        margin-bottom: 15px; }
      i {
        font-size: 16px;
        padding-right: 15px; }
      &:hover {
        color: $bluey-purple; } }
    &__btn {
      @include r(767) {
        display: none; }
      a {
        padding: 15px 17px;
        display: block !important;
        color: $white;
        font-size: 16px;
        line-height: 1.07;
        border: 1px solid $bluey-purple;
        background-color: $bluey-purple;
        box-shadow: 0px 2px 10px 0 rgba(49, 47, 47, 0.16);
        &:hover {
          color: $bluey-purple;
          background: none; }
        i {
          padding-right: 10px; } } } } }
.header {
  width: 100%;
  padding: 40px 0;
  position: absolute;
  &__page {
    background: #fcfcfc;
    position: relative;
    nav {
      li:nth-child(1) a {
        color: $slate-grey !important;
        cursor: pointer !important; }
      li:nth-child(2) a {
        color: $bluey-purple;
        cursor: default; } } }
  @include r(767) {
    padding: 20px 0;
    background: #dddce4; }
  .tel {
    display: none;
    position: absolute;
    right: 30px;
    font-size: 26px;
    line-height: 0.66;
    color: $bluey-purple;
    @include r(767) {
      display: block; } }
  .menu-open {
    width: 40px;
    height: 30px;
    padding-top: 15px;
    position: absolute;
    display: inline-block;
    cursor: pointer;
    top: 0px;
    left: 20px;
    transition: all 0.3s;
    display: none;
    @include r(767) {
      display: block; }
    span {
      width: 25px;
      height: 4px;
      background: $battleship-grey-two;
      display: block;
      transition: all 0.3s;
      border-radius: 20%;
      &:before {
        content: '';
        position: absolute;
        left: 0;
        width: 25px;
        height: 4px;
        background: $battleship-grey-two;
        transition: all 0.3s;
        top: 6px;
        border-radius: 20%; }
      &:after {
        content: '';
        position: absolute;
        left: 0;
        bottom: 2px;
        width: 25px;
        height: 4px;
        background: $battleship-grey-two;
        transition: all 0.3s;
        border-radius: 20%; } } }
  &__logo {
    display: flex;
    align-items: center;
    @include r(1024) {
      justify-content: center;
      padding-bottom: 30px; }
    @include r(767) {
      padding-bottom: 0; }
    img {
      padding-right: 8px;
      @include r(767) {
        width: 60px; } }
    h2 {
      font-family: 'Rodondo';
      text-transform: uppercase;
      color: $slate-grey;
      font-size: 32px;
      font-weight: 900;
      line-height: 0.98;
      letter-spacing: 0.9px;
      position: relative;
      display: inline-block;
      p {
        font-family: 'Ubuntu';
        font-size: 8.3px;
        font-weight: 500;
        color: $greyish-two;
        letter-spacing: 0.2px;
        position: absolute;
        right: 5px; } } }
  nav {
    @include r(767) {
      display: none; }
    ul {
      display: flex;
      justify-content: space-between;
      li {
        display: inline-block;
        &:nth-child(1) {
          a {
            color: $bluey-purple;
            cursor: default;
            &:hover {
              color: $bluey-purple; } } }
        a {
          color: $slate-grey;
          font-size: 16px;
          font-weight: 500;
          display: inline-block;
          text-transform: uppercase;
          padding-bottom: 4px;
          &:hover {
            color: $bluey-purple; } } } } } }
