.choice {
  background: linear-gradient(to left, #8653c8, #9666d0, #9666d0, #9666d0, #8a57cb);
  box-shadow: 0px 2px 10px 0 rgba(49, 47, 47, 0.16);
  padding-top: 60px;
  padding-bottom: 80px;
  @include r(1024) {
    padding: 50px 0;
    padding-bottom: 10px; }
  &__block {
    @include r(1024) {
      margin-bottom: 40px;
      margin-left: 40px; }
    @include r(430) {
      margin-left: 28px; }
    p {
      font-size: 16px;
      font-weight: 500;
      color: $white-two;
      position: relative;
      width: 130px;
      display: inline-block;
      text-align: left;
      @include r(767) {
        font-size: 14px;
        width: 120px; }
      i {
        font-size: 29.9px;
        color: $white-two;
        position: absolute;
        left: -55px;
        top: 5px;
        @include r(430) {
          left: -37px; } } } } }
