.header .menu-open {
  width: 40px;
  height: 30px;
  padding-top: 15px;
  position: absolute;
  display: inline-block;
  cursor: pointer;
  top: 0px;
  left: 20px;
  transition: all 0.3s;
  display: none;
}

@media only screen and (max-width: 767px) {
  .header .menu-open {
    display: block;
  }
}

.header .menu-open span {
  width: 25px;
  height: 4px;
  background: #72787d;
  display: block;
  transition: all 0.3s;
  border-radius: 20%;
}

.header .menu-open span:before {
  content: '';
  position: absolute;
  left: 0;
  width: 25px;
  height: 4px;
  background: #72787d;
  transition: all 0.3s;
  top: 6px;
  border-radius: 20%;
}

.header .menu-open span:after {
  content: '';
  position: absolute;
  left: 0;
  bottom: 2px;
  width: 25px;
  height: 4px;
  background: #72787d;
  transition: all 0.3s;
  border-radius: 20%;
